
.logo_22 {
  font-size: 2.2rem;
  font-weight: bold;
  display: inline-block;
  font-style: italic;
}
.main-layout > .fish.layout {
  /* background: #f0f2f5; */
  color: #666;
}
.main-layout > .fish.layout > .header{
  /* background: #fff; */
  padding: 0 1em;
}
.main-layout > .fish.layout > .header {
  margin-bottom: 3px;
}
.main-layout > .fish.layout > .sider {
  /*background: #fff;*/
  /* padding-top: 20px; */
  padding-bottom: 20px;
}
.main-layout > .fish.layout > .content {
  /* background: #fff; */
  padding: 20px;
}
.fish.card .hljs {
  padding: 0;
  background-color: #ffffff;
  margin: 0;
}
code, pre {
  tab-size: 2;
  padding: 0;
  margin: 0;
}
table code, ul li code, .code-card code:not(.hljs) {
  margin: 0 3px;
  background: #f7f7f7;
  padding: .2em .4em;
  border-radius: 3px;
  font-size: .9em;
  border: 1px solid #eee;
  display: inline-block;
  margin-bottom: .2em;
}
ul.api li {
  margin: 10px;
}
.fish.table.attributes table thead tr th:first-child {
  width: 16%;
}
.fish.table.attributes table thead tr th:nth-child(3) {
  width: 20%;
}
.fish.table.attributes table thead tr th:nth-child(4) {
  width: 10%;
}



